@use "../abstract" as *;

.main-header__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  min-height: 66px;
  width: 320px;
  margin: 0 auto;
}

.main-header__logo img {
  text-decoration: none;
  color: #000000;
}

.main-header__logo-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.main-header__button {
  position: relative;
  width: 24px;
  height: 18px;
  border: none;
  appearance: none;
  background-color: transparent;
  cursor: pointer;
}

.main-header__navigation--closed .main-header__button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}

.main-header__navigation--closed .main-header__button:active::before {
  background-color: #575757;
  opacity: 0.3;
  box-shadow: 0 8px 0 0 #575757, 0 16px 0 0 #575757;
}

.main-header__navigation--open .main-header__button--open {
  top: 8px;
  position: absolute;
  right: 39px;
  left: auto;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 24px;
    left: 18px;
    width: 25px;
    height: 2px;
    background-color: #222222;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
    box-shadow: none;
  }

  &:active::before,
  &:active::after {
    background-color: rgba(1, 1, 1, 0.3);
    box-shadow: none;
  }
}

.main-header__navigation--closed .navigation {
  display: none;
}

.main-header__navigation--open .navigation {
  top: 0;
}



