@media (min-width: $tablet-width) {
  .main-header__navigation {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    min-height: 82px;
    padding-top: 20px;
    min-width: 768px;
    margin: 0 auto;
    padding-left: 64px;
    padding-right: 64px;
  }

  .main-header__wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .main-header__button {
    display: none;
  }
}

@media (min-width: $desktop-width) {
  .main-header__navigation {
    width: 1280px;
    justify-content: space-between;
    padding-top: 50px;
    padding-left: 0;
    padding-right: 0;
  }

  .main-header__wrapper {
    margin: 0;
  }
}
